<template>
  <div>
    <h1 class="post-title">{{ this.post.name }}</h1>
    <p class="post-subtitle">{{ this.post.date }} ~ kusunburnu</p>
    <p class="post-content" id="post-content"></p>
  </div>
</template>

<script>
import posts from "../posts/posts.json";
export default {
  name: "Post",
  data() {
    return {
      post: {},
      path: "",
    };
  },
  created() {
    this.getPost();
  },
  mounted() {
    this.mountPost();
  },
  methods: {
    createRoutes() {
      posts.postList.forEach((post) => {
        this.$router.addRoute({
          path: post.path,
          name: post.name,
          component: () => import("../views/Post.vue"),
        });
      });
    },
    getPost() {
      let post = posts.postList.find((post) => post.path === this.$route.path);
      this.post = post;
    },
    mountPost() {
      let post = posts.postList.find((post) => post.path === this.$route.path);
      document.getElementById("post-content").innerHTML = post.content;
    },
  },
};
</script>
